<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row type="flex">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" class="input-with-select">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" style="width: 100%" border>
        <el-table-column type="index" label="#" align="center"></el-table-column>
        <el-table-column prop="order_number" label="" align="center"></el-table-column>
        <el-table-column prop="order_price" label="" align="center"></el-table-column>
        <el-table-column prop="pay_status" label="" align="center">
          <template slot-scope="scope">
            <el-tag type="success" size="mini" v-if="scope.row.order_pay === '1'"></el-tag>
            <el-tag type="warning" size="mini" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" align="center"></el-table-column>
        <el-table-column prop="create_time" label="" align="center">
          <template slot-scope="scope">
            <div>{{ scope.row.create_time | dateFormat }}</div>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template>
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="editOrder"></el-button>
            <el-button type="success" icon="el-icon-s-tools" size="mini" @click="showProgress"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- -->
    <el-dialog
      title=""
      :visible.sync="editOrderDialogVisible"
      width="50%"
      @close="editOrderDialogClosed"
    >
      <span>
        <el-form
          :model="editOrderForm"
          :rules="editOrderFormRules"
          ref="editOrderFormRef"
          label-width="100px"
        >
          <el-form-item label="/" prop="city">
            <el-cascader
              v-model="editOrderForm.editOrderList"
              :options="cityData"
              :props="editOrderProps"
              @change="editOrderChange"
              clearable
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="editOrderForm.address" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editOrderDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editOrderDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="showProgreseDialogVisible">
      <span>
        <el-timeline>
          <el-timeline-item
            v-for="(item, index) in progressInfo"
            :key="index"
            :timestamp="item.ftime"
          >{{item.context}}</el-timeline-item>
        </el-timeline>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import citys from './citydata'
export default {
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5,
      },
      total: 0,
      orderList: [],
      editOrderDialogVisible: false,
      //   
      editOrderFormRules: {
        city: [{ required: true, message: '', trigger: 'blur' }],
        address: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      // 
      editOrderForm: {
        address: '',
        editOrderList: [],
      },
      cityData: citys,
      //   
      editOrderProps: {
        expandTrigger: 'hover',
      },
      //   
      showProgreseDialogVisible: false,
      //   
      progressInfo: [],
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    //   
    async getOrderList() {
      const { data: res } = await this.$http.get(`orders`, {
        params: this.queryInfo,
      })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.orderList = res.data.goods
      this.queryInfo.pagenum = parseInt(res.data.pagenum)
      this.total = res.data.total
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    editOrder() {
      this.editOrderDialogVisible = true
    },
    // editOrderChange
    editOrderChange() {},
    // 
    editOrderDialogClosed() {
      this.editOrderForm.editOrderList = []
      this.editOrderForm.address = ''
    },
    // showProgress
    async showProgress() {
      const { data: res } = await this.$http.get(`/kuaidi/1106975712662`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.progressInfo = res.data
      this.showProgreseDialogVisible = true
    },
  },
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>